<template>
  <el-row>
    <div class="content-box">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <el-col :span="16">
          <div class="abstact-form">
            <el-form ref="abstactForm" :model="abstactForm" :rules="rules" :inline="true">
              <el-form-item label=" " prop="userName">
                <el-input v-model="abstactForm.userName"></el-input>
              </el-form-item>
              <el-form-item label=" " prop="company">
                <el-input v-model="abstactForm.company"></el-input>
              </el-form-item>
              <el-form-item label="" prop="telephone">
                <el-input v-model.trim="abstactForm.telephone" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model.trim="abstactForm.email" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-upload
                  ref="upload"
                  :action="upDataForm.upUrl"
                  :on-preview="handlePreview"
                  :on-remove="handleRemove"
                  :file-list="fileList"
                  :auto-upload="false">
                  <el-button slot="trigger" size="small" type="primary"></el-button>
                  <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">
                  </el-button>
                  <span>PDFword10M</span>
                </el-upload>
              </el-form-item>
              <el-form-item class="abstactForm-button">
                <el-button type="success" @click="saveAbstact('abstactForm')"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="abstact-download">
            <h1></h1>
            <a href="javascript:void(0)"><span> - .docx</span></a>
          </div>
        </el-col>
      </el-card>
    </div>
  </el-row>
</template>

<script type="text/ecmascript-6">
  /**
   * Created by xzc
   * :
   */
  import {registerSave, upDataUrl} from '../../http/apiResource'
  export default {
    data () {
      return {
        rules: {
          userName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          company: [
            {required: true, message: '', trigger: 'blur'}
          ],
          telephone: [
            {required: true, message: '', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        abstactForm: {
          userName: '',
          company: '',
          telephone: '',
          email: ''
        },
        upDataForm: {
          'isUpload': false,
          'upUrl': upDataUrl
        },
        fileList: []
      }
    },
    methods: {
      saveAbstact(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.post(registerSave, this.abstactForm).then(res => {
            })
          }
        })
      },
      submitUpload() {
        this.$refs.upload.submit()
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handlePreview(file) {
        console.log(file)
      }
    }
  }
</script>

<style scoped>
  .content-box {
    width: 75%;
    margin: 0 auto;
  }
  .abstact-download {
    width: 100%;
    margin-top: 5rem;
  }

  .abstact-download h1 {
    display: block;
    text-align: center;
    color: #013763;
    font-size: 1.2rem;
    line-height: 4rem;
    font-family: sans-serif;
    border-bottom: 0.1rem solid #f15a22;
  }

  .abstact-download span {
    display: block;
    text-align: center;
    line-height: 3rem;
    font-family: sans-serif;
    color: #3a8ee6;
    font-size: 0.9rem;
  }

  .abstact-form span {
    color: #f15a22;
    font-family: sans-serif;
  }

  .abstactForm-button {
    margin-left: 10rem;
  }
</style>



